.wrap {
    display: inline-block;
    position: relative;
    height: 80px;
    padding: 0;
    position: relative;
}

.input-1[type="text"] {
    height: 60px;
    font-size: 25px;
    display: inline-block;
    font-family: "Lato";
    font-weight: 100;
    border: none;
    outline: none;
    color: #222;
    padding: 3px;
    padding-right: 60px;
    width: 0px;
    position: absolute;
    top: 0;
    right: 0;
    background: none;
    z-index: 3;
    transition: width .4s cubic-bezier(0.000, 0.795, 0.000, 1.000);
    cursor: pointer;
}

.input-1[type="text"]:focus:hover {
    border-bottom: 1px solid #BBB;
    background-color: #fff;
}

.input-1[type="text"]:focus {
    width: 800px;
    z-index: 1;
    border-bottom: 1px solid #BBB;
    cursor: text;
    background-color: #fff;
    border-radius: 8px;
}

.input-1[type="submit"] {
    height: 69px;
    width: 1.9vw;
    display: inline-block;
    color: red;
    background: url(data:image/png;base64,) center center no-repeat;
    background-size: 110%;
    text-indent: -10000px;
    border: none;
    position: absolute;
    top: 5px;
    right: 0;
    left: -15px;
    z-index: 2;
    opacity: 0.8;
    cursor: pointer;
    transition: opacity .4s ease;
    border-radius: 8px;
}

.input-1[type="submit"]:hover {
    opacity: 0.8;
    background-color: #fff;
}

@media (min-width: 1920px) and (max-width: 2560px) {
    .wrap {
        height: 65px;
